import './WithdrawFunds.css';
import TextInput from '../../../components/TextInput/TextInput';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';
import { useState } from 'react';

function WithdrawFunds() {
  const radioOptions = [
    {
      id: 1,
      value: 'card',
      label: '',
    },
    {
      id: 2,
      value: 'bank-account',
      label: ' ',
    },
  ];

  const [sum, setSum] = useState();
  const [focus, setFocus] = useState(false);
  const [toggleValue, setToggleValue] = useState('card');
  
  function handleSumChange(e) {
    setSum(e.target.value)
  };

  function handleSumFocus() {
    setFocus(true)
  };

  function handleSumBlur() {
    setFocus(false)
  };

  function  toggleCheckbox(e) {
    setToggleValue(e)
  }

  const commissionValue = () => {
    if(typeof(sum) === 'undefined') {
      return 0
    } else {
      return `${sum * 5 /100}`
    }
  }
  
  const commissionClassName = (
    `${focus ? 'withdraw__commission' : 'withdraw__commission_invisible'}`
  );

  return (
    <form className='withdraw tabs__form'>
      <div className='withdraw__info-container'>
        <h2 className='withdraw__title'> </h2>
        <p className='withdraw__subtitle'>    1000  + 5%  .        .</p>
        <p className='withdraw__ratio'><span className='withdraw__ratio-icon'/>: 1  = 1 </p>
      </div>
      <div className='withdraw__sum-container'>
        <label className='input__title'>
           
          <input className='input' placeholder=' ' onChange={handleSumChange} onFocus={handleSumFocus} onBlur={handleSumBlur} type='number'></input>
        </label>
        <p className={commissionClassName}> : {commissionValue()} </p>
        <RadioFilter options={radioOptions} title={' '} onChange={toggleCheckbox} toggleValue={toggleValue}/>
      </div>
        {
          toggleValue === 'card' ?
          <div className='withdraw__data-container'>
            <TextInput title={' '} placeholder={' '} inputClassName={'input__title_limited_width'}/>
            <TextInput title={' '} placeholder={'  '} inputClassName={'input__title_limited_width'}/>
          </div>
          :
          <div className='withdraw__data-container'>
            <TextInput title={' '} placeholder={'  '} />
            <TextInput title={''} placeholder={' '} />
            <TextInput title={''} placeholder={' '} />
          </div>
        }
      <button className='withdraw__button' type='submit'></button>
    </form>
  )
}

export default WithdrawFunds;